.App {
  text-align: center;

}



.App-content {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(16px + 2vmin);
  color: white;

}

h1{
  cursor: pointer;
}





.gif-options{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}


a {
  text-decoration: none;
  color: white;
  border: 2px white solid;
  border-radius: 10px;
  margin: 10px;
  padding: 15px;
 
}

a:hover{
  background-color: white;
  color: #282c34;
}